import React, { useState, useEffect } from 'react';
import { MY_URL } from "../../utils/constans";
import './Documents.css'
import Header from "../Header/Header";
import Footer from "../Footer/Footer";
import { NavLink } from 'react-router-dom';

function Documents(props) {
  const [anketaData, setAnketaData] = useState({});
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const token = localStorage.getItem("token");
    //        
    fetch("/anketa", {
      method: "GET",
      headers: {
        "Authorization": `Bearer ${token}`
      }
    })
    .then(response => {
      if (response.status === 401) {
        console.log(' ');
      } else if (response.status !== 200) {
        console.log('Looks like there was a problem. Status Code: ' +
          response.status);
        return;
      }
      return response.json();
    })
    .then(data => {
      setAnketaData(data); //     
      setLoading(false);
    })
    .catch(error => {
      console.log('Fetch Error :-S', error);
    });
  }, []);

  if (loading) {
    return <div>Loading...</div>;
  }

  if (Object.keys(anketaData).length === 0) {
    return (
      <>
      <Header loggedIn={props.loggedIn} theme={"header_theme_dark"} />
      <main className='main'>
        <h4>   , ,  .</h4>
        <NavLink to="/anketa" className='usefull__anketa'> </NavLink>

      </main>
      <Footer />
    </>

    );
  }

  const documentNamesSyd = [
    {id: 1, text: '       ', file: '______'},
    {id: 2, text: '  ', file: '__'},
    {id: 3, text: '    ', file: '_'},
  ];
  const documentNamesVne = [
    {id: 1, text: '        ', file: '_____'},
    {id: 2, text: '    ', file: '__'},
  ];

  function docs(docArr) {
    return documentNamesSyd.map(doc => (
    <div key={doc.id}>
          <li>
            {doc.text}{" "}
            <ul ><div className='usefull__doc'>
            <li> </li>
            <div className='usefull__dows'>
              <button
              className="documents__btn documents__btn1"
              onClick={() => handleDownload(doc.file+'.docx')}
            >
              
            </button>
            <button
              className="documents__btn documents__btn1"
              onClick={() => handlePreview(doc.file+'_.pdf')}
            >
              
            </button>
            </div>
              </div>

              <div className='usefull__doc'>
              <li > </li>
            <div className='usefull__dows'>
            <button
              className="documents__btn documents__btn1"
              onClick={() => handleDownload(doc.file+'.pdf')}
            >
              
            </button>
            <button
              className="documents__btn documents__btn1"
              onClick={() => handlePreview(doc.file+'.pdf')}
            >
              
            </button>
            </div>
              </div>

            </ul>
          </li>
    </div>
  ))};



  const bankruptcyConclusion = anketaData.bankruptcyConclusion;

  const docSyd = (Object.keys(bankruptcyConclusion).length > 0) &&
    (<><div>{docs(documentNamesSyd)}
            <button
              className="documents__btn"
              onClick={() => handleDownload("__.zip")}
            >
                
            </button>
            <button
              className="documents__btn"
              onClick={() => handleDownload("__.zip")}
            >
                
            </button>
            <button
              className="documents__btn"
              onClick={() => handleDownload("__.zip")}
            >
                
            </button>

    </div></>);
  const docVne = (Object.keys(bankruptcyConclusion).length > 0) &&
    (bankruptcyConclusion === 'Both') &&
    (<><div>{docs(documentNamesVne)}

            <button
              className="documents__btn"
              onClick={() => handleDownload("__.zip")}
            >
                
            </button>
            <button
              className="documents__btn"
              onClick={() => handleDownload("__.zip")}
            >
                
            </button>
            <button
              className="documents__btn"
              onClick={() => handleDownload("__.zip")}
            >
                
            </button>


    </div></>);

  let doc = 'def';
  if (bankruptcyConclusion === 'Both') {
    doc = 'both';
  } else if (bankruptcyConclusion === 'HaveTo') {
    doc = 'haveto';
  } else if (bankruptcyConclusion === 'NotHaveTo') {
    doc = 'nothaveto';
  } else {
    doc = 'less';
  }


  const handleDownload = (filename) => {
    const token = localStorage.getItem("token");

    //      
    const headers = {
      Authorization: `Bearer ${token}`
    };

    //       
    fetch(`${MY_URL}/files/${filename}`, {
      method: "GET",
      headers: headers
    })
      .then((response) => {
        // ,    
        if (!response.ok) {
          throw new Error("   ");
        }
        return response.blob(); //      blob
      })
      .then((blob) => {
        //  URL    blob
        const url = window.URL.createObjectURL(new Blob([blob]));
        //     
        const link = document.createElement("a");
        link.href = url;
        link.setAttribute("download", filename); //     
        document.body.appendChild(link);
        link.click();
        link.parentNode.removeChild(link);
      })
      .catch((error) => {
        console.error(":", error);
      });
  };


  const handlePreview = (filename) => {
    //     
    window.open(`${MY_URL}/files/${filename}`, '_blank');
  };

  return (
    <>
      <Header loggedIn={props.loggedIn} theme={"header_theme_dark"} />
      <main className='main'>
      {(doc === "both") && (
        <>
        <h2>    .  :</h2>
        <div className='doc__pam'>
        <p>     </p>
        <button className="documents__btn" onClick={() => handleDownload('.pdf')}></button>
        <button className="documents__btn" onClick={() => handlePreview('.pdf')}></button>
        </div>


        <div className='documents-container'>
          <div className='documents-column'>
            <h3>    </h3>
            {docSyd}
            <a className='doc__more' href='/usefull#syd'>   </a>
            <NavLink to='/adress' className='navlink'>  </NavLink>
          </div>
          <div className='documents-column'>
            <h3>     </h3>

            {docVne}
            <a className='doc__more' href='/usefull#vne'>   </a>
            <p>   <a className='documents__btn documents__btn1 documents__btn2' rel="noopener noreferrer" target="_blank" href='https://xn--d1achjhdicc8bh4h.xn--p1ai/search/mfc'><b></b></a>       </p>
          </div>
        </div>

        </>
      )}
      {(doc !== "both") && (
        <>
        <h2>     .  :</h2>
        <div className='doc__pam'>
        <p>     </p>
        <button className="documents__btn" onClick={() => handleDownload('.pdf')}></button>
        <button className="documents__btn" onClick={() => handlePreview('.pdf')}></button>
        </div>
        <div>
          {docSyd}
          <a className='doc__more' href='/usefull#syd'>   </a>
          <NavLink to='/adress' className='navlink'>  </NavLink>
        </div>
        </>
      )}

      </main>
      <Footer />
    </>
  )
}

export default Documents;